@import '../../../../sourses/styles/variebles.scss';

.produc_list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 20px;

  padding-top: 50px;
  padding-bottom: 50px;
  background-color: rgb(218, 218, 218);
}
@media screen and (min-width: 768px) {
  .produc_list {
    flex-direction: row;
  }
}
